@font-face {
  font-family: 'Popins Medium';
  src: url('./assets/fonts/Poppins-Medium.ttf');
}
@font-face {
  font-family: 'Popins Regular';
  src: url('./assets/fonts/Poppins-Regular.ttf');
}
@font-face {
  font-family: 'Poppins Light';
  src: url('./assets/fonts/Poppins-Light.ttf');
}
@font-face {
  font-family: 'CinzelDecorative Bold';
  src: url('./assets/fonts/CinzelDecorative-Bold.ttf');
}

@font-face {
  font-family: 'CinzelDecorative Black';
  src: url('./assets/fonts/CinzelDecorative-Black.ttf');
}

@font-face {
  font-family: 'CinzelDecorative Regular';
  src: url('./assets/fonts/CinzelDecorative-Regular.ttf');
}

:root{
  --text-color: #fff;
  --text-on-white: #000;
  --current-font-family: 'Popins Regular';
}

body {
  margin: 0;
  font-family: var(--current-font-family);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(0deg, rgba(40, 40, 40, 0.2), rgba(40, 40, 40, 0.2)), #231F43;
  color: var(--text-color);
  max-width: 100%;
  overflow-x: hidden;
  
}
#root{
  display:flex;
  flex-direction: column;
  height:100vh;
  overflow: hidden;
}
header{
  flex: 0 0 auto;
}
main{
  flex: 1 0 auto;
}
#root{
  background-image: url('./assets/img/bg.png');
  background-repeat: no-repeat;
  background-position:center center;
  background-size: auto 100%;
}
footer{
  flex: 0 0 auto;
}
ul,p{
  margin-top: 0;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img{
  max-width: 100%;
}

button{
  font-family:var(--current-font-family);
  color: var(--text-color);
  background-color:unset;
  border: none;
  padding: 5px 10px;
  white-space: nowrap;
  cursor: pointer;
}

