.header-nav-list,
.footer-content-paragraph{
  margin:0px;
}
.footer-content-paragraph{
  color: var(--text-on-white)
}

.footer-content-bg{
  position: relative;
  z-index: 1;
}
.footer-content-bg::before{
  content:'';
  position: absolute;
  width: 210%;
  height: 280%;
  left:50%;
  top:50%;
  transform: translate(-50%,-50%);
  background-image: url('./assets/svg/footer-shape.svg');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  z-index: -1;
}
.logo{
  width: 35%;
  max-width: 300px;
  min-width: 150px;

}
.footer-add-img{
  position:absolute;
  left:0;bottom:0;
  width: 25%;
  max-width: 100%;
  height: 70vh;
  max-height: calc(100% - 135px);
  /* overflow: hidden */
  z-index: 1;
}
.footer-add-img::before{
  content:"";
  position:absolute;
  left: 0%;
  bottom: 10%;
  /* transform: translateY(-50%); */
  z-index: -1;
  max-width:500px;
  width: 120%;
  height: 60%;
  /* background-color: red; */
  background-image: url("./assets/svg/bg-shape.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: left center;
}
.footer-add-img img{
  position:absolute;
  width: auto;
  height: 100%;
  object-fit: contain;
}
.container-min{
  width: 100%;
  max-width: 835px;
  margin: 0 auto;
  
}
.text-center{
  text-align: center;
}
.modal{
  position: relative;
  z-index: 5;
  padding: 70px 110px;
  box-sizing: border-box;
  max-height: 60vh;
  /* overflow-y: auto; */
}
.modal::before{
  content:"";
  position: absolute;
  width: 100%;
  height: 100%;
  left:0;
  top: 0;
  z-index: -1;
  background: #000000;
opacity: 0.7;
filter: blur(30px);
  /* box-shadow:inset 0px 0px 20px 40px rgba(0, 0, 0, 0.5) , 0px 0px 20px 40px rgba(0, 0, 0, 0.5)  ; */
}

.justify-content-center{
  justify-content: center;
}
.location{
  position: absolute;
  bottom: 60px;
  left: 50%;
  transform: translateX(-50%);
  
}


.location .bottom{
  font-family: 'CinzelDecorative Bold';
}
